.wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 100px;
    padding: 9px 16px;
    border-radius: 4px;
    font-size: 1.6rem;
    font-weight: 700;
    font-family: 'ProximaNova', sans-serif;
    background-color: var(--white);
    border: 1px solid transparent;
    user-select: none;
    cursor: pointer;

    +.wrapper {
        margin-left: 8px;
    }

    &.disabled {
        pointer-events: none;
        opacity: 0.5;
    }
}

.icon+.title,
.title+.icon {
    margin-right: 8px;
}

.icon {
    display: inline-block;
    text-align: center;
    width: 24px;
}

.primary {
    background-color: var(--bg-top-card);
    color: var(--bg-bottom-card) !important;
    border-radius: 15px;
    padding: 15px 30px;
    font-weight: normal;

    &:hover {
        color: var(--bg-top-card) !important;
        background-color: var(--bg-bottom-card);
    }
}

.secondary {
    color: var(--bg-top-card) !important;
    background-color: var(--bg-bottom-card);
    border-radius: 15px;
    padding: 15px 30px;
    font-weight: normal;

    &:hover {
        background-color: var(--bg-top-card);
        color: var(--bg-bottom-card) !important;
    }
}