.footer {
    background-color: var(--bg-secondary);
    border-radius: var(--border-round-primary);
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 40px;
}

.wrapper {
    position: relative;
    border: 1px dashed var(--white);
    border-radius: var(--border-round-primary);
    padding: 20px 10px;
    overflow: hidden;

    .fox {
        position: absolute;
        bottom: -2%;
        left: -5%;

        &>img {
            max-width: 300px;
        }
    }
}

.left {
    width: 66.66%;
}

.link {
    &:hover {
        color: var(--bg-bottom-card);
    }
}

.right {
    margin-left: 5px;
    width: 33.33%;
    border: 1px dashed var(--white);
    border-radius: var(--border-round-primary);

    img {
        width: 100%;
        height: auto;
        border-radius: var(--border-round-primary);
        transform: rotate(-5deg);
    }
}

@media (max-width:1050px) {
    .wrapper {
        .fox {
            left: -8%;
        }
    }

    .left {
        width: 66.66%;
    }

    .right {
        width: 33.33%;
    }
}

@media (max-width:769px) {
    .wrapper {
        flex-wrap: wrap;
        justify-content: center;
        flex-direction: column-reverse;

        .fox {
            left: -15%;
        }
    }

    .left {
        margin-top: 20px;
        width: 100%;
    }

    .right {
        width: 100%;

        img {
            width: 100%;
            height: auto;
            border-radius: var(--border-round-primary);
            transform: rotate(-5deg);
        }
    }
}

@media (max-width: 426px) {
    .wrapper {
        .fox {
            left: -20%;

            &>img {
                max-width: 200px;
            }
        }
    }
}