@import 'normalize.css';

@font-face {
    font-family: 'Montserrat';
    src: url('../assets/fonts/Montserrat/Montserrat-ExtraBold.ttf');
    font-weight: 900;
}

@font-face {
    font-family: 'Montserrat';
    src: url('../assets/fonts/Montserrat/Montserrat-Bold.ttf');
    font-weight: 700;
}

@font-face {
    font-family: 'Montserrat';
    src: url('../assets/fonts/Montserrat/Montserrat-SemiBold.ttf');
    font-weight: 600;
}

@font-face {
    font-family: 'Montserrat';
    src: url('../assets/fonts/Montserrat/Montserrat-Regular.ttf');
    font-weight: 400;
}

:root {
    --text-color: #2C3E50;
    --white: #fff;
    --black: #000;
    --bg-primary: #FEF7F1;
    --bg-secondary: #374885;
    --bg-top-card: #F8E9A2;
    --bg-bottom-card: #F76C6C;
    --border-round-primary: 10px;
    --border-round-secondary: 25px 0;
    --border-round-card: 20px 20px 10px 10px;
    --width: 1200px;
    --height-header: 100px;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

button,
input,
[tabindex] {
    outline: none;
    border: none;
}

li {
    list-style: none;
}

a[href] {
    color: var(--white);
    text-decoration: none;
}

body {
    color: var(--white);
    font-family: 'Montserrat', sans-serif;
    font-size: 1.6rem;
    background-color: var(--bg-primary);
    padding: 10px 5.35vw;
    margin-right: 16px;
    text-rendering: optimizeSpeed;
}

.w-full {
    width: 100%;
}

.h-full {
    height: 100%;
}

.mt-1 {
    margin-top: 10px;
}

.mb-1 {
    margin-bottom: 10px;
}

.d-flex {
    display: flex;
}

.flex-wrap {
    flex-wrap: wrap;
}

.d-grid {
    display: grid;
}

.grid-auto-fit {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.place-items-center {
    place-items: center;
}

.gap-1 {
    gap: 10px;
}

.items-center {
    align-items: center;
}

.justify-center {
    justify-content: center;
}

.justify-space-between {
    justify-content: space-between;
}

.justify-start {
    justify-content: start;
}

.text-center {
    text-align: center;
}
.text-left{
    text-align: left;
}

.text-primary{
    color: var(--bg-bottom-card);
}

.uppercase {
    text-transform: uppercase;
}

.lower {
    text-transform: lowercase;
}

.font-900 {
    font-weight: 900;
}

.font-700 {
    font-weight: 900;
}

.font-600 {
    font-weight: 600;
}

.font-400 {
    font-weight: 400;
}

.text-black {
    color: var(--black);
}

.cursor-pointer {
    cursor: pointer;
}

.title {
    font-size: 2.4rem;
    line-height: 3.6rem;
    padding-bottom: 10px;
}

.title-2 {
    font-size: 3.2rem;
    line-height: 3.6rem;
    padding-bottom: 10px;
    color: var(--text-color);
    font-weight: 600;
    padding-bottom: 10px;
}

.lesson-content {
    width: 100%;
    height: 800px;
}

.active {
    color: var(--bg-bottom-card);
}

.desc {
    font-size: 1.8rem;
    line-height: 2.4rem;
    padding-bottom: 10px;
}


@media (max-width:1024px) {
    .title {
        font-size: 1.8rem;
        line-height: 2.6rem;
    }

    .desc {
        font-size: 1.4rem;
        line-height: 2rem;
    }

    .title-2 {
        font-size: 2.4rem;
        line-height: 2.6rem;
    }

    .lesson-content {
        width: 100%;
        height: 500px;
    }
}

@media (max-width:426px) {
    .title {
        font-size: 1.6rem;
        line-height: 2rem;
    }

    .desc {
        font-size: 1.2rem;
        line-height: 1.8rem;
    }

    .title-2 {
        font-size: 2rem;
    }

    .lesson-content {
        width: 100%;
        height: 250px;
    }
}