.wrapper {
    margin-top: 20px;
    margin-bottom: 15px;
    padding: 20px 0px 80px 0px;
    background: url('../../assets/images/bg-header-card.jpg'), linear-gradient(60deg, #543ab7 0, #00acc1 100%);
    border-radius: 25px 25px 0 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: #24305e;

    .title {
        color: var(--text-color);
        font-weight: 600;
        font-size: 2.4rem;
        line-height: 3.6rem;
    }

}

@media (max-width:768px) {
    .wrapper {
        padding: 20px 0px 50px 0px;

        .title {
            padding: 0 10px;
            font-size: 2rem;
            line-height: 2.6rem;
        }
    }
}