.fox {
    position: fixed;
    top: 35%;
    right: 0;
    z-index: 998;

    .fox-img {
        max-width: 200px;
        max-height: 200px;
        cursor: pointer;
    }
}

.fox-content {
    border-radius: 10px;
    background-color: var(--bg-secondary);
    padding: 20px 10px;
    min-width: 350px;
    overflow-y: auto;
    max-height: 600px;

    &::-webkit-scrollbar {
        width: 0;

    }

    .wrapper {
        border: 1px dashed var(--white);
        border-radius: 10px;
        padding: 10px;
    }

    .title {
        padding-bottom: 20px;
    }

    .topic {

        // .topic-item {}

        .topic-link {
            &:hover {
                color: var(--bg-top-card);
            }
        }
    }

    .lesson {
        padding: 10px 5px;

        .lesson-item {
            margin-bottom: 10px;
        }

        .lesson-link {
            &:hover {
                color: var(--bg-bottom-card);
            }
        }
    }

    .iconLeft {
        margin-right: 5px;
        color: var(--bg-top-card);
    }

    .iconChuDe {
        margin-right: 5px;
        color: var(--bg-bottom-card);
    }

    .active {
        color: var(--bg-bottom-card);
    }
    

}

@media (max-width: 769px) {
    .fox {
        .fox-img {
            max-width: 90px;
            max-height: 90px;
        }
    }

    .fox-content {
        padding: 20px 10px;
        min-width: 250px;

        .title {
            font-size: 14px;
        }

        .topic {

            .topic-link {
                font-size: 12px;
            }
        }

        .lesson {
            padding: 10px 20px;

            .lesson-item {
                margin-bottom: 10px;
            }

            .lesson-link {
                font-size: 12px;
            }
        }
    }
}