.card {
    background-color: var(--bg-top-card);
    width: 280px;
    height: 344px;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.2s linear;

    .top {
        width: 100%;
        height: 150px;
        border-radius: 20px 20px 10px 10px;
        background: linear-gradient(#7fc354, #fff);
        padding-top: 10px;

        &>img {
            width: 120px;
            height: 120px;
        }
    }

    .bottom {
        padding: 0 40px;
        font-size: 1.8rem;
        color: var(--bg-bottom-card);

        .title {
            padding-top: 2rem;
        }
    }

    &:hover {
        transform: translateY(-7px);
    }
}

.card:nth-child(2n) {
    background-color: var(--bg-bottom-card);

    .title {
        color: var(--bg-top-card);
    }
}