.resources {
    border: 1px solid hsl(0, 0%, 80%);
    margin: 0 3vw;

    .content {
        padding: 10px 20px;
    }

    .wrapper {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        gap: 10px;

        .item {
            background-color: var(--bg-top-card);
            width: 100%;
            height: 100px;
            border-radius: 10px;
            border: 1px solid #ccc;
            display: flex;
            align-items: center;
            gap: 20px;
            transition: all .25s linear;
            padding: 0 10px;
            overflow: hidden;

            .img {
                width: 80px;
                height: 80px;
            }

            .title {
                color: var(--text-color);
            }

            &:hover {
                background-color: var(--bg-secondary);
            }

            &:hover .title {
                color: var(--white);
            }
        }

        .item:nth-child(2n) {
            background-color: hsla(0, 90%, 70%, .25);

            &:hover {
                background-color: var(--bg-secondary);
            }
        }
    }

}

.tabList {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    background-color: #f1f1f1;
    border-bottom: 1px solid #ccc;

    .tabBtn {
        width: 100%;
        height: 40px;
        cursor: pointer;
    }

    .tabBtn.active {
        background-color: var(--bg-top-card);
    }
}