.wrapper {
    margin-bottom: 10px;
}

.pre-header {
    margin-bottom: 10px;
}

.logo-foxdag0,
.logo-hnmu {
    width: 100px;
    height: 100px;
}

.logo-foxdag0 {
    border-radius: 7px;
    width: 96.29px;
}

.logo-hnmu {
    border-radius: 50%;
}

.header {
    background-color: var(--bg-secondary);
    height: var(--height-header);
    border-radius: var(--border-round-primary);
    padding: 10px;
}

.header-inner {
    border: 1px dashed var(--white);
    border-radius: var(--border-round-primary);
    height: 80px;

}

.list {
    width: 66.66%;
}

.item {
    padding: 0 40px;
    // position: relative;

    // &:hover .sub-menu {
    //     display: block;
    // }
}

.link {
    font-size: 2.4rem;
    line-height: 7.82rem;
    user-select: none;

    &:hover {
        color: var(--bg-bottom-card);
    }

}

.wrap-sub-menu {
    margin-top: 14.8px;
}


.sub-menu {
    // display: none;
    // position: absolute;
    padding: 10px 0;
    background-color: var(--bg-secondary);
    border-left: 1px dashed var(--white);
    border-right: 1px dashed var(--white);
    border-bottom: 1px dashed var(--white);
    border-radius: 0 0 10px 10px;
    z-index: 999;

    &>ul {

        &>li {
            margin-top: 5px;

            .icon {
                padding-left: 10px;
            }

            .sub-menu-link {
                padding: 0 10px;
                font-size: 1.8rem;
                line-height: 3.6rem;
                white-space: nowrap;

                &:hover {
                    color: (var(--bg-bottom-card));
                }


            }

            &:hover .icon {
                color: var(--bg-top-card);
            }
        }
    }
}

.menuBar {
    display: none;
}

@media (max-width:1232px) {
    .item {
        padding: 0 20px;
    }

    .link {
        font-size: 1.8rem;
    }
}

@media (max-width:838px) {
    .item {
        padding: 0 10px;
    }

    .link {
        font-size: 1.4rem;
    }
}

@media (max-width:605px) {

    .logo-foxdag0,
    .logo-hnmu {
        width: 70px;
        height: 70px;
    }

    .item {
        padding: 0 10px;
    }

    .link {
        font-size: 1rem;
        line-height: 1.7rem;
    }

    .sub-menu-link {
        font-size: 0.8rem !important;
    }
}

@media (max-width: 472px) {
    .menu {
        display: none !important;
    }

    .link {
        font-size: 1.6rem;
    }

    .menuBar {
        display: block;

    }

    .menuBar-content {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        background-color: var(--black);
        z-index: 1000;
        width: 100%;
        opacity: 0.92;
        transform: translateX(-100%);
        transition: all 0.4s ease-in-out;
    }

    .show {
        transform: translateX(0);
    }

    .menuBar-wrapper {
        position: absolute;
        left: 0;
        background-color: var(--bg-secondary);
        height: 100%;
        width: 250px;
    }

    .menuBar-list {
        margin: auto auto;
    }

    .menuBar-item {
        width: 250px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.2s linear;

        &:hover {
            background-color: var(--bg-bottom-card);
        }
    }

    .menuBar-sub-item {
        &>li {
            height: 30px;
        }
    }

    .menuBar-link {
        padding: 10px 0;
    }

    .close {
        position: absolute;
        top: 5%;
        right: 10%;
        font-size: 2rem;
        cursor: pointer;
    }

    .bar-icon {
        font-size: 2.5rem;
        margin-left: 10px;
        cursor: pointer;
    }
}