.home {}

.slideshow {
    position: relative;
    margin-bottom: 70px;

    .inner {
        max-width: 800px;
        margin: 0 auto;

        .item {
            &>a>img {
                border-radius: 25px;
                width: 100%;
                height: auto;
            }
        }
    }

    .control {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 3%;
        right: 3%;

        .prev,
        .next {
            color: var(--white);
            user-select: none;
            background-color: rgba(0, 0, 0, .5);
            padding: 16px;
            cursor: pointer;
            font-weight: 700;
            transition: .6s ease;
            border-radius: 5px;

            &:hover {
                background-color: var(--black);
            }
        }
    }
}

.introduce {
    &-desc {
        width: 50%;
    }

    &-img>img {
        width: 100%;
        height: auto;
    }
}

@media (max-width: 677px) {
    .introduce {
        flex-wrap: wrap;

        &-desc {
            width: 100%;
        }

        &:last-child {
            flex-wrap: wrap-reverse;
        }
    }
}